import React, { useState } from 'react'
import { Select } from 'antd'
const { Option } = Select

export default function WorkDay({ value = {}, onChange, getPopupContainer }) {
  const [start, setStart] = useState()
  const [end, setEnd] = useState()

  const triggerChange = (changedValue) => {
    const temp = {
      start,
      end,
      ...changedValue,
    }

    if (temp.start && temp.end) {
      onChange(temp)
    }
  }

  const handleStart = (newStart) => {
    setStart(newStart)
    triggerChange({
      start: newStart,
    })
  }

  const handleEnd = (newEnd) => {
    setEnd(newEnd)
    triggerChange({
      end: newEnd,
    })
  }

  return (
    <span>
      <Select
        value={value.start || start}
        onChange={handleStart}
        placeholder=""
        className="m-wrok-day-select"
        getPopupContainer={getPopupContainer}
      >
        <Option value=""></Option>
        <Option value=""></Option>
        <Option value=""></Option>
        <Option value=""></Option>
        <Option value=""></Option>
        <Option value=""></Option>
        <Option value=""></Option>
      </Select>
      <span className="m-wrok-day-text"></span>
      <Select
        value={value.end || end}
        onChange={handleEnd}
        placeholder=""
        className="m-wrok-day-select"
        getPopupContainer={getPopupContainer}
      >
        <Option value=""></Option>
        <Option value=""></Option>
        <Option value=""></Option>
        <Option value=""></Option>
        <Option value=""></Option>
        <Option value=""></Option>
        <Option value=""></Option>
      </Select>
    </span>
  )
}
