import { Form, Col, Input, Button } from 'antd'
import {
  Icon
} from '../../../../../../components/light'


//
const getColumns = (props) => {
  return [
    {
      title: 'ID',
      dataIndex: 'id',
    },
    {
      title: '',
      dataIndex: 'name',
    },
    {
      title: '',
      //width: 220,
      render: (record) => {
        return (
          <div className="m-action">
            <Button
              className="m-action-btn"
              size="small"
              danger
              onClick={() => props.onDelete(record)}
            >
              
            </Button>
            <Button
              className="m-action-btn"
              size="small"
              onClick={() => props.onCheck(record)}
            >
              
            </Button>
            <Button
              className="m-action-btn"
              size="small"
              onClick={() => props.onEdit(record)}
            >
              
            </Button>
          </div>
        )
      },
    },
  ]
}

//
const getSearchFields = (isExpand) => {
  // eslint-disable-next-line
  const expandClassname = `m-table-serach ${isExpand ? 'active' : ''}`
  const span = 6
  return (
    <>
      <Col span={span}>
        <Form.Item name="name" label="">
          <Input placeholder="IDID" />
        </Form.Item>
      </Col>
    </>
  )
}

//
const getModalFields = () => {
  return (
    <>
      <Form.Item
        label=""
        name="name"
        rules={[
          {
            required: true,
            message: '',
          },
        ]}
      >
        <Input />
      </Form.Item>
    </>
  )
}

//Tool
const getToolBtnList = ({onOperate}) => {
  return (
    <>
      <div className="m-content-tool-row">
        <Button
          className="m-space"
          type="primary"
          onClick={() => onOperate({ type: 0 })}
        >
          <Icon name="correct" className="m-tool-btn-icon"></Icon>
          
        </Button>
      </div>
    </>
  )
}

export { getColumns, getSearchFields, getModalFields, getToolBtnList }
